import React from 'react';
import { Card, CardImg, CardImgOverlay,
    CardTitle, Breadcrumb, BreadcrumbItem,Row,Col,CardSubtitle,CardBody,Button, CardText } from 'reactstrap';
import { Link } from 'react-router-dom';
import mypic from '../shared/images/ford.png';
        


function RenderCarList({car}){
    
    return(
        <Card width="100%">
            <Row>
            <Col md={3}>
                <CardImg top width="100%" src={car.image} alt={car.carModal} height="100%"  />
            </Col>
            <Col md={9}>
                <CardBody>
                <CardTitle>{car.carModal}</CardTitle>
                <CardSubtitle>Card subtitle</CardSubtitle>
                <CardText>Some quick example text to build on the card title and make up the bulk of the card's content.</CardText>
                <Link to={`/cars/${car.id}`} ><Button>Button</Button></Link>
                </CardBody>
            </Col>
            </Row>
        </Card>
      
        
    );
}


const Cars  = (props) => {

    const carslist = props.cars.map((car)=>{
        return (
               <RenderCarList car={car} /> 
        );
    })

    return(
        <div className="container">
            <div className="row">
                <Breadcrumb>
                    <BreadcrumbItem><Link to="/">Cars</Link></BreadcrumbItem>
                    <BreadcrumbItem active></BreadcrumbItem>
                </Breadcrumb>
                <div className="col-12">
                    <h3>Cars for rent</h3>
                    <hr />
                </div>         
            </div>
            {carslist}       

        </div>
    );
    
}

export default Cars;